<template>
  <div class="container login-container">
        <div class="row">
            <div class="login-form-1">
                <h3>Ingreso</h3>
                <form @submit.prevent="login" >
                    <div class="form-group">
                        <input
                            type="text"
                            class="form-control"
                            placeholder="Usuario"
                            name='usuario'
                            v-model="usuario.usuario"
                        />
                    </div>
                    <div class="form-group">
                        <input
                            type="password"
                            class="form-control"
                            placeholder="ContraseÃ±a"
                            name='password'
                            v-model="usuario.password"
                        />
                    </div>
                    <div class="form-group">
                        <input 
                            type="submit"
                            class="btnSubmit"
                            value="Login" 
                        />
                    </div>
                </form>
            </div>
         </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
    
    data(){
        return{
            usuario:{}
        }
    },
    
    computed:{
        ...mapGetters('auth',['getChecking','getPerfil'])
    },
    methods:{
        ...mapActions('auth',['startLogin']),
        async login(){
            var user={
                usuario: this.usuario.usuario,
                password: this.usuario.password
            }
            await this.startLogin(user)
            if(this.getChecking){
                if(this.getPerfil === 'admin'){

                    this.$router.push('rifa')
                }
            }
        }
    }
    
}
</script>

<style scoped>

.login-container{
    margin-top: 15%;
}
.login-form-1{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
}
.login-form-1 h3{
    text-align: center;
    color: #333;
}
.login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
    background: #0062cc;
}
.login-form-2 h3{
    text-align: center;
    color: #fff;
}
.login-container form{
    padding: 10%;
}
.btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
}
.login-form-1 .btnSubmit{
    color: #fff;
    background-color: #0062cc;
}
.login-form-2 .btnSubmit{
    background-color: #fff;
    color: #0062cc;
}
.login-form-2 .ForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
}
.login-form-1 .ForgetPwd{
    color: #0062cc;
    font-weight: 600;
    text-decoration: none;
}

</style>